import React from 'react';
import ApiService from "./ApiService";
 
class Login extends React.Component {
	constructor(props){
		super(props);
			
        this.state={
			input_id:null,
			input_pw:null
		}	
	}
	
    onClickLogin = () => {
		var param={
			userId:this.state.input_id,
			userPw:this.state.input_pw
		};
		
		ApiService.onLogin(param)
			.then(res=>{
				console.log(res);
			})
    }
 
	onChange=(e)=>{
		console.log("name: " + e.target.name);
		console.log("value: " + e.target.value);
		console.log("value: " + this.state.input_id);

		this.setState({[e.target.name]:e.target.value})
	}
	
	render(){
	    return(
	        <div>
	            <h2>Login</h2>
	            <div>
	                <label htmlFor='input_id'>ID : </label>
	                <input type='text' name='input_id' onChange={this.onChange} />
	            </div>
	            <div>
	                <label htmlFor='input_pw'>PW : </label>
	                <input type='password' name='input_pw' onChange={this.onChange} />
	            </div>
	            <div>
	                <button type='button' onClick={this.onClickLogin}>Login</button>
	            </div>
	        </div>
	    )
	}
}
 
export default Login;